/* Css Button */
:root {
  --text-color: hsla(210, 50%, 85%, 1);
  --shadow-color: hsla(210, 40%, 52%, 0.4);
  --btn-color: hsl(210, 80%, 42%);
  --bg-color: #141218;
}

* {
  box-sizing: border-box;
}

html,
body {
  background-color: #020202;
}

.end-button-section {
  width: 20rem;
  position: relative;
  top: 18rem;
  left: 5rem;
}
button {
  position: relative;
  padding: 30px 30px;
  border: none;
  background: none;
  cursor: pointer;
  font-family: "Source Code Pro";
  font-weight: 900;
  text-transform: uppercase;
  font-size: 20px;
  color: hsla(210, 50%, 85%, 1);
  color: var(--text-color);
  background-color: hsl(210, 80%, 42%);
  background-color: var(--btn-color);
  box-shadow: hsla(210, 40%, 52%, 0.4) 2px 2px 22px;
  box-shadow: var(--shadow-color) 2px 2px 22px;
  border-radius: 4px;
  z-index: 0;
  overflow: hidden;
}

button:focus {
  outline-color: transparent;
  box-shadow: hsl(210, 80%, 42%) 2px 2px 22px;
  box-shadow: var(--btn-color) 2px 2px 22px;
}

.right::after,
button::after {
  content: var(--content);
  display: block;
  position: absolute;
  white-space: nowrap;
  padding: 40px 40px;
  pointer-events: none;
}

button::after {
  font-weight: 200;
  top: -30px;
  left: -20px;
}

.right,
.left {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
}

.right {
  left: 66%;
}

.left {
  right: 66%;
}

.right::after {
  top: -30px;
  left: calc(-66% - 20px);
  background-color: #141218;
  background-color: var(--bg-color);
  color: transparent;
  transition: -webkit-transform 0.4s ease-out;
  transition: transform 0.4s ease-out;
  transition: transform 0.4s ease-out, -webkit-transform 0.4s ease-out;
  -webkit-transform: translate(0, -90%) rotate(0deg);
          transform: translate(0, -90%) rotate(0deg);
}

button:hover .right::after {
  -webkit-transform: translate(0, -47%) rotate(0deg);
          transform: translate(0, -47%) rotate(0deg);
}

button .right:hover::after {
  -webkit-transform: translate(0, -50%) rotate(-7deg);
          transform: translate(0, -50%) rotate(-7deg);
}

button .left:hover ~ .right::after {
  -webkit-transform: translate(0, -50%) rotate(7deg);
          transform: translate(0, -50%) rotate(7deg);
}

/* bubbles */

button::before {
  content: "";
  pointer-events: none;
  opacity: 0.6;
  background: radial-gradient(
      circle at 20% 35%,
      transparent 0,
      transparent 2px,
      hsla(210, 50%, 85%, 1) 3px,
      hsla(210, 50%, 85%, 1) 4px,
      transparent 4px
    ),
    radial-gradient(
      circle at 75% 44%,
      transparent 0,
      transparent 2px,
      hsla(210, 50%, 85%, 1) 3px,
      hsla(210, 50%, 85%, 1) 4px,
      transparent 4px
    ),
    radial-gradient(
      circle at 46% 52%,
      transparent 0,
      transparent 4px,
      hsla(210, 50%, 85%, 1) 5px,
      hsla(210, 50%, 85%, 1) 6px,
      transparent 6px
    );
  background: radial-gradient(
      circle at 20% 35%,
      transparent 0,
      transparent 2px,
      var(--text-color) 3px,
      var(--text-color) 4px,
      transparent 4px
    ),
    radial-gradient(
      circle at 75% 44%,
      transparent 0,
      transparent 2px,
      var(--text-color) 3px,
      var(--text-color) 4px,
      transparent 4px
    ),
    radial-gradient(
      circle at 46% 52%,
      transparent 0,
      transparent 4px,
      var(--text-color) 5px,
      var(--text-color) 6px,
      transparent 6px
    );
  width: 100%;
  height: 300%;
  top: 0;
  left: 0;
  position: absolute;
  -webkit-animation: bubbles 5s linear infinite both;
          animation: bubbles 5s linear infinite both;
}
@-webkit-keyframes bubbles {
  from {
    -webkit-transform: translate();
            transform: translate();
  }
  to {
    -webkit-transform: translate(0, -66.666%);
            transform: translate(0, -66.666%);
  }
}
@keyframes bubbles {
  from {
    -webkit-transform: translate();
            transform: translate();
  }
  to {
    -webkit-transform: translate(0, -66.666%);
            transform: translate(0, -66.666%);
  }
}

